@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Tangerine&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;300;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300;700&display=swap);
* {
  --nav-container-height: 50px;
}

.nav-container {
  height: var(--nav-container-height);
  margin-top: 10px;
  /* position: absolute; */
}

nav {
  height: 100%;
  width: 1300px;
  margin: auto;
}

.logo {
  height: 100%;
  width: 175px;
  background-color: var(--main-color);
  font-family: 'Libre Baskerville', sans-serif;
  font-size: 2.5rem;
  color: white;
  padding-top: 2px;
}

.homepage {
  margin-top: 10px;
}

.main-container {
  height: 1400px;
  width: 1300px;
  margin: auto;
  grid-gap: 30px;
  gap: 30px;
}

.position-fixed {
  overflow: hidden;
}

.search {
  height: 650px;
  width: 1300px;
  grid-gap: 20px;
  gap: 20px;
}

.search-container {
  height: 100%;
  width: 30%;
  grid-gap: 5px;
  gap: 5px;
}

.search-container header {
  height: 75px;
  width: 100%;
  background-color: rgb(248, 161, 46);
}
.search-container header h2 {
  font-family: 'Comfortaa';
  font-weight: 100;
  color: white;
  font-size: 3rem;
  letter-spacing: 0.9rem;
  padding-left: 1rem;
  padding-top: 5px;
}

.search-bar {
  width: 100%;
  height: 3rem;
}

/*Search Bar*/
.search-bar div {
  height: 3rem;
  width: 100%;
}

.search-bar input {
  width: 100%;
  border: none;
  background-color: white;
  font-family: 'Roboto', sans-serif;
  font-weight: 300;
  font-size: 1.2rem;
  color: black;
  padding-left: 10px;
  border: solid thin var(--green-ish);
}

.list-of-artist-container {
  width: 100%;
  height: 100%;
  overflow: auto;
  grid-gap: 5px;
  gap: 5px;
}

.individual-artist {
  width: 100%;
  background-color: var(--green-ish);
  font-size: 1.5rem;
  padding: 15px 0 15px 10px;
  color: white;
  font-family: 'Comfortaa';
  border: solid thin white;
  text-align: left;
}

.individual-artist:hover {
  background-color: rgb(248, 161, 46);
  cursor: pointer;
}

::-webkit-scrollbar {
  width: 7px;
  margin-right: 5px;
}

::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.8);
  border-radius: 10px;
}

.results {
  height: 100%;
  width: 70%;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-template-rows: repeat(8, 1fr);
  grid-gap: 5px;
  gap: 5px;
}

/*Main Image*/
.results > div:nth-child(1) {
  grid-column: 1 / 9;
  grid-row: 1 / 6;
  background-color: var(--green-ish);
  background-size: 100%;
  background-position: center;
}

.results-info {
  height: 100%;
  width: 100%;
  color: #f2e1a6;
  font-family: 'Roboto', sans-serif;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 20px;
  transition-duration: 500ms;
}

.results-info:hover {
  background: transparent;
  color: transparent;
  transition-duration: 500ms;
  cursor: pointer;
}

.results-info h1 {
  font-weight: 100;
  font-size: 5rem;
  margin-bottom: 10px;
}

.results-info > h2 {
  font-size: 2rem;
  font-weight: 100;
  color: white;
  transition-duration: 500ms;
}

.results-info:hover > h2 {
  background: transparent;
  color: transparent;
  transition-duration: 500ms;
}

/*Container for related Search results*/
.results > div:nth-child(2) {
  grid-column: 1 / 9;
  grid-row: 6 / 9;
  display: grid;
  grid-gap: 5px;
  gap: 5px;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(3, 1fr);
}

.related-results > div {
  background-size: 150%;
  background-position: center;
}

.related-results > div div {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  transition-duration: 500ms;
}

.related-results > div div:hover {
  background-color: transparent;
  transition-duration: 500ms;
  cursor: pointer;
}

.related-results div:nth-child(1) {
  grid-column: 1 / 4;
  grid-row: 1 / 4;
}

.related-results div:nth-child(2) {
  grid-column: 4 / 7;
  grid-row: 1 / 4;
}

.related-results div:nth-child(3) {
  grid-column: 7 / 10;
  grid-row: 1 / 4;
}

.top {
  height: 650px;
  grid-gap: 20px;
  gap: 20px;
}

/*=========================================*/
/*LEFT SIDE OF TOP*/
/*=========================================*/
.top-left {
  height: 100%;
  width: 60%;
  /* gap: 5px; */
}

/*-------------------------------------*/
.featured-artist-container {
  height: 100%;
  width: 100%;
  grid-gap: 5px;
  gap: 5px;
}

.featured-artist-container header {
  height: 75px;
  width: 100%;
  background-color: rgb(248, 161, 46);
}

.featured-artist-container header h2 {
  font-family: 'Comfortaa', cursive;
  font-weight: 100;
  color: white;
  font-size: 3rem;
  letter-spacing: 0.9rem;
  padding-left: 1rem;
  padding-top: 5px;
}

.featured-artist-container .sub-container {
  height: 100%;
  width: 100%;
  grid-gap: 5px;
  gap: 5px;
}

.sub-container .featured-artist:nth-child(1) {
  background-size: 500px;
  background-position: center top;
}

.sub-container .featured-artist:nth-child(2) {
  background-size: 500px;
  background-position: center;
}

.featured-artist {
  height: 100%;
  width: 50%;
  justify-content: flex-end;
}

.featured-artist:hover {
  cursor: pointer;
}

.featured-artist-description {
  height: 15%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  font-family: 'Roboto', sans-serif;
  color: #f2e1a6;
  padding: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
}
/*=========================================*/
/*RIGHT SIDE OF TOP*/
/*=========================================*/
.top-right {
  height: 100%;
  width: 40%;
  grid-gap: 5px;
  gap: 5px;
}

/*------------------------------*/
.random-art {
  height: 100%;
  width: 100%;
  grid-gap: 5px;
  gap: 5px;
}

.random-art header {
  height: 75px;
  width: 100%;
  background-color: rgb(248, 161, 46);
  position: relative;
}

.random-art header button {
  position: absolute;
  height: 100%;
  width: 22px;
  top: 0;
  right: 0;
  background-color: var(--main-color);
  border: none;
}
.random-art header button:active {
  /* background-color: var(--main-color--accent); */
  box-shadow: inset 1px 4px 4px rgba(0, 0, 0, 0.4);
}

.random-art header h2 {
  font-family: 'Comfortaa', cursive;
  font-weight: 100;
  color: white;
  font-size: 3rem;
  letter-spacing: 0.9rem;
  padding-left: 1rem;
  padding-top: 5px;
}

.random-art section {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  grid-gap: 5px;
  gap: 5px;
}

.random-art section div {
  background-size: 300px;
  background-position: center;
  transition-duration: 500ms;
}

.random-art section div:hover {
  background-size: 350px;
  transition-duration: 500ms;
  cursor: pointer;
}

.random-art section div div:nth-child(1) {
  height: 80%;
  width: 100%;
}

.random-art section div div:nth-child(2) {
  height: 20%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.random-art-card h3 {
  font-size: 1rem;
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
  color: #f2e1a6;
  padding: 10px;
}

* {
  --image-scale: 200px;
}

.model-container {
  height: 100vh;
  width: 100vw;
  position: fixed;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.6);
}

.model {
  height: 650px;
  width: 1350px;
  background-color: rgba(255, 255, 255, 1);
  border-radius: 5px;
  box-shadow: 2px 2px 2px 1px rgba(0, 0, 0, 0.2);
  position: relative;
  overflow: hidden;
}

.left {
  background-color: white;
  height: 100%;
  width: 100%;
  padding: 40px 20px 20px 20px;
}

.current-image {
  height: 100%;
  width: 100%;
  background-color: rgb(236, 236, 236);
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
}

.current-image-info {
  font-family: 'Roboto';
  grid-gap: 5px;
  gap: 5px;
  padding: 5px;
  padding-top: 20px;
}

.current-image-info h3 {
  font-weight: 700;
  font-style: italic;
}

.current-image-info h4 {
  padding-top: 5px;
  font-weight: 500;
  color: rgb(151, 137, 61);
}

.current-image-info h5 {
  font-size: 0.8rem;
  font-weight: 500;
}

/*=========================================*/
.right {
  height: 100%;
  width: 610px;
  padding: 40px 17px 20px 0;
  background-color: white;
}

.images-grid {
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: repeat(2, var(--image-scale));
  grid-template-rows: repeat(5, var(--image-scale));
  grid-auto-columns: var(--image-scale);
  grid-auto-rows: var(--image-scale);
  grid-gap: 2px;
  gap: 2px;
  /* border: solid red; */
  overflow: auto;
}

.image-div {
  background-color: rgb(236, 236, 236);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  cursor: pointer;
}

.model button {
  position: absolute;
  top: 3px;
  right: 3px;
  height: 30px;
  width: 30px;
  font-size: 2rem;
  border: none;
  color: lightgray;
  background-color: white;
  font-family: 'Comfortaa';
  font-weight: 700;
  cursor: pointer;
}

* {
  margin: 0;
  box-sizing: border-box;

  --main-color: rgb(234, 27, 27);
  --main-color-accent: #ea9898;

  --green-ish: rgb(131, 210, 210);
  --green-ish-accent-1: rgb(124, 198, 198);
  --green-ish-accent-2: rgb(102, 195, 195);
  --green-ish-accent-3: rgb(118, 202, 202);

  --blue-ish: rgb(183, 221, 244);
  --blue-ish-accent: rgb(146, 201, 236);
}

.display-none {
  display: none;
}

.center-item {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-row {
  display: flex;
}

.flex-col {
  display: flex;
  flex-direction: column;
}

